body {
  --height-header: 60px;
  --active-link-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__item {
  line-height: var(--height-header);
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
}

.menu__link.is-active {
  color: var(--active-link-color);
  position: relative;
}

.menu__link.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.menu__link:hover {
  color: var(--active-link-color);
}

